.adventureStory {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  min-width: 1440px;
  min-height: 688px;
  overflow: hidden;
  background-color: #fbf7e9;
  z-index: 100;
}

.close-adventureStory-button {
  z-index: 52;
  position: absolute;
  top: 8.67%;
  right: 7.1%;
  width: 3.43%;
}

.close-adventureStory-button:hover {
  -webkit-animation-name: close-adventureStory-button-hover;
          animation-name: close-adventureStory-button-hover;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

@-webkit-keyframes close-adventureStory-button-hover {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@keyframes close-adventureStory-button-hover {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

.close-adventureStory-button.isHiding {
  -webkit-animation-name: close-adventureStory-button-isHiding-scale;
          animation-name: close-adventureStory-button-isHiding-scale;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

@-webkit-keyframes close-adventureStory-button-isHiding-scale {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}

@keyframes close-adventureStory-button-isHiding-scale {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}

.move-left-button,
.move-right-button {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 2.91%;
  z-index: 20;
}

.move-left-button {
  left: 4.17%;
}

.move-right-button {
  right: 4.17%;
}

.adventureStory-img-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  height: 75%;
  overflow: hidden;
}

.adventureStory-img-wrapper::before,
.adventureStory-img-wrapper::after {
  content: "";
  display: block;
  height: 100%;
  width: 200px;
  position: absolute;
  top: 0;
  z-index: 10;
}

.adventureStory-img {
  width: 4386px;
  height: 812px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: absolute;
  top: 0;
}

.adventureStory-img-item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.adventureStory-background {
  z-index: 2;
}

.adventureStory-json {
  z-index: 3;
}

.adventureStory-text {
  z-index: 4;
}

.eason-think {
  position: absolute;
  bottom: 8.21%;
  right: 27.07%;
  width: 7.43%;
  z-index: 5;
  display: none;
  cursor: pointer;
}

.eason-think.show {
  display: block;
  -webkit-animation-name: eason-think-button-show;
          animation-name: eason-think-button-show;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

@-webkit-keyframes eason-think-button-show {
  0% {
    opacity: 0;
  }
  60% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}

@keyframes eason-think-button-show {
  0% {
    opacity: 0;
  }
  60% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}

.dot {
  -webkit-animation: dot-wave 2s linear infinite;
          animation: dot-wave 2s linear infinite;
}

.dot:nth-child(2) {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

.dot:nth-child(3) {
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
}

.dot:nth-child(4) {
  -webkit-animation-delay: 1.2s;
          animation-delay: 1.2s;
}

.dot:nth-child(5) {
  -webkit-animation-delay: 1.6s;
          animation-delay: 1.6s;
}

.dot:nth-child(6) {
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}

.dot:nth-child(7) {
  -webkit-animation-delay: 2.4s;
          animation-delay: 2.4s;
}

@-webkit-keyframes dot-wave {
  0%,
  60%,
  100% {
    -webkit-transform: initial;
            transform: initial;
  }
  30% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
}

@keyframes dot-wave {
  0%,
  60%,
  100% {
    -webkit-transform: initial;
            transform: initial;
  }
  30% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
}



