.storyA {
  position: relative;
  width: 100vw;
  height: 100vh;
  background: url("./storyA_background.png") no-repeat center;
  background-size: cover;
  overflow: hidden;
  min-width: 1440px;
  min-height: 688px;
}

.storyA-background {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
}

.storyA .sound {
  position: absolute;
  bottom: 15%;
  right: 16.86%;
}

.dialogG,
.dialogH,
.dialogI {
  display: none;
  position: absolute;
}

.dialogG {
  width: 17.97%;
  bottom: 90.6%;
  right: 31.71%;
}

.dialogH {
  width: 16.95%;
  bottom: 71.27%;
  right: 13.05%;
}

.dialogI {
  width: 17.97%;
  bottom: 56.54%;
  right: 31.71%;
}

.dialogG.show,
.dialogH.show,
.dialogI.show {
  display: block;
}

.dialogG-text,
.dialogG-background,
.dialogH-text,
.dialogH-background,
.dialogI-text,
.dialogI-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.dialogG-text,
.dialogH-text,
.dialogI-text {
  z-index: 3;
  display: none;
}

.dialogG-text.show,
.dialogH-text.show,
.dialogI-text.show {
  display: block;
}

.dialogG-background,
.dialogH-background,
.dialogI-background {
  z-index: 2;
}

.dialogG-background.isShowing,
.dialogH-background.isShowing,
.dialogI-background.isShowing {
  -webkit-animation-name: dialogGHI-background-show;
          animation-name: dialogGHI-background-show;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

@-webkit-keyframes dialogGHI-background-show {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  80% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes dialogGHI-background-show {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  80% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.dialogG-background.isHiding,
.dialogH-background.isHiding,
.dialogI-background.isHiding {
  -webkit-animation-name: dialogGHI-background-hide-scale,
    dialogGHI-background-hide-opacity;
          animation-name: dialogGHI-background-hide-scale,
    dialogGHI-background-hide-opacity;
  -webkit-animation-duration: 0.8s, 0.3s;
          animation-duration: 0.8s, 0.3s;
  -webkit-animation-delay: 0s, 0.5s;
          animation-delay: 0s, 0.5s;
  -webkit-animation-timing-function: ease-in-out, ease-in-out;
          animation-timing-function: ease-in-out, ease-in-out;
  -webkit-animation-fill-mode: forwards, forwards;
          animation-fill-mode: forwards, forwards;
}

@-webkit-keyframes dialogGHI-background-hide-scale {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
}

@keyframes dialogGHI-background-hide-scale {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
}

@-webkit-keyframes dialogGHI-background-hide-opacity {
  0% {
    opacity: 1;
  }
  60% {
    opacity: 0.4;
  }
  100% {
    opacity: 0;
  }
}

@keyframes dialogGHI-background-hide-opacity {
  0% {
    opacity: 1;
  }
  60% {
    opacity: 0.4;
  }
  100% {
    opacity: 0;
  }
}

.reply-rabbit {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  display: none;
}

.reply-rabbit.show {
  display: block;
  -webkit-animation-name: reply-rabbit-button-show;
          animation-name: reply-rabbit-button-show;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

.reply-rabbit-button {
  cursor: pointer;
}

@-webkit-keyframes reply-rabbit-button-show {
  0% {
    opacity: 0;
  }
  60% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}

@keyframes reply-rabbit-button-show {
  0% {
    opacity: 0;
  }
  60% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}

.dot {
  -webkit-animation: dot-wave 2s linear infinite;
          animation: dot-wave 2s linear infinite;
}

.dot:nth-child(2) {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

.dot:nth-child(3) {
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
}

.dot:nth-child(4) {
  -webkit-animation-delay: 1.2s;
          animation-delay: 1.2s;
}

.dot:nth-child(5) {
  -webkit-animation-delay: 1.6s;
          animation-delay: 1.6s;
}

@-webkit-keyframes dot-wave {
  0%,
  60%,
  100% {
    -webkit-transform: initial;
            transform: initial;
  }
  30% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
}

@keyframes dot-wave {
  0%,
  60%,
  100% {
    -webkit-transform: initial;
            transform: initial;
  }
  30% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
}

.buttonB,
.buttonC {
  display: none;
  position: absolute;
  width: 22.55%;
  right: 26.71%;
}

.buttonB.show,
.buttonC.show {
  display: block;
}

.buttonB {
  bottom: 90.6%;
}

.buttonC {
  bottom: 56.54%;
}

.buttonB-text,
.buttonB-background,
.buttonC-text,
.buttonC-background {
  position: absolute;
