  left: 0;
  width: 100%;
}

.buttonB-text,
.buttonC-text {
  z-index: 3;
  display: none;
}

.buttonB-text.show,
.buttonC-text.show {
  display: block;
}

.buttonB-background.isShowing,
.buttonC-background.isShowing {
  -webkit-animation-name: buttonBC-background-show;
          animation-name: buttonBC-background-show;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

@-webkit-keyframes buttonBC-background-show {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  80% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes buttonBC-background-show {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  80% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}



