  width: 26.96%;
}

.patternF {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 43.08%;
  right: 30.38%;
  width: 1.81%;
}

.patternF.show {
  visibility: visible;
  -webkit-animation-name: patternF-show-opacity, patternF-show-scale;
          animation-name: patternF-show-opacity, patternF-show-scale;
  -webkit-animation-duration: 1s, 1s;
          animation-duration: 1s, 1s;
  -webkit-animation-timing-function: ease-in-out, ease-in-out;
          animation-timing-function: ease-in-out, ease-in-out;
  -webkit-animation-fill-mode: forwards, forwards;
          animation-fill-mode: forwards, forwards;
}

@-webkit-keyframes patternF-show-opacity {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes patternF-show-opacity {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes patternF-show-scale {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }

  80% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes patternF-show-scale {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }

  80% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.rabbitA {
