}

@-webkit-keyframes answer-move {
  from {
    -webkit-transform: rotate(0deg) translateX(10px) rotate(0deg);
            transform: rotate(0deg) translateX(10px) rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg) translateX(10px) rotate(-360deg);
            transform: rotate(360deg) translateX(10px) rotate(-360deg);
  }
}

@keyframes answer-move {
  from {
    -webkit-transform: rotate(0deg) translateX(10px) rotate(0deg);
            transform: rotate(0deg) translateX(10px) rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg) translateX(10px) rotate(-360deg);
            transform: rotate(360deg) translateX(10px) rotate(-360deg);
  }
}

.answer.active path {
  -webkit-animation: answer-active 1s ease-in;
          animation: answer-active 1s ease-in;
  fill: #f0bc29;
}

@-webkit-keyframes answer-active {
  0% {
    fill: #e57452;
  }

  100% {
    fill: #f0bc29;
  }
}

@keyframes answer-active {
  0% {
    fill: #e57452;
  }

  100% {
